<template>
  <div class="message-wrap">
    <div class="title">{{ $t('admin.groupList') }}</div>
    <div class="no-data" v-if="list.length === 0">
      {{ $t('common.noData') }}
    </div>
    <MessageItem
      v-for="item in list"
      :key="item.id"
      :dataSource="item"
    ></MessageItem>
    <svg
      class="icon"
      aria-hidden="true"
      @click="$gbUtils.handleToPage('/releaseMessage')"
    >
      <use xlink:href="#fabu"></use>
    </svg>
  </div>
</template>

<script>
import { handleMessageList } from '@/services/teamLeader'

import MessageItem from './components/MessageItem.vue'
export default {
  components: { MessageItem },
  name: '',
  data () {
    return {
      list: []
    }
  },
  methods: {
    async getList () {
      const resp = await handleMessageList()
      if (!resp.success) return
      this.list = resp.rows
    }
  },
  mounted () {
    this.getList()
  }
}
</script>

<style scoped lang="scss">
.message-wrap {
  position: relative;
  background-color: #1d1d1d;
  min-height: 100vh;
  padding: 20px 15px;
  color: #dbd9d5;
  .title {
    font-size: 16px;
    font-weight: 600;
    padding: 20px 0;
  }
  .icon {
    position: fixed;
    right: 10px;
    top: 70%;
    width: 45px;
    height: 45px;
  }
}
</style>
